<template>
  <div class="right-panel">
    <div class="right-panel-inner">
        <div class="logo"></div>
        <div class="title">Supertiger Chat</div>
        <Spinner msg="Logging in..." v-if="alreadyLoggedIn && userDetails == null && loggedIn" />
        
        <transition name="component-fade" appear mode="out-in" v-else>

          <LoggedIn v-if="loggedIn && userDetails != null" />

          <div class="new-member" v-if="!loggedIn">
            <div class="details">
              Supertiger chat is the best chat client to be made with 99% uptime, you won't miss a thing! Join now if you’re new!
            </div>
            <div class="switch-buttons">
              <div :class="{button: true, selected: loginSelected}" @click="loginSelected = true">Already a pro</div>
              <div :class="{button: true, selected: !loginSelected}" @click="loginSelected = false">I'm new!</div>
            </div>
            <transition name="switch-selected" mode="out-in">
              <LoginPanel v-if="loginSelected" />
              <SignupPanel v-if="!loginSelected" />
            </transition>
          </div>
        </transition>
    </div>
  </div>
</template>

<script>
import {bus} from '../../main';
import SignupPanel from "../../components/homePage/SignupPanel.vue"
import LoginPanel from "../../components/homePage/LoginPanel.vue"
import LoggedIn from "../../components/homePage/LoggedIn.vue"
import Spinner from "../../components/Spinner.vue"
export default {
  components: {
    SignupPanel,
    LoginPanel,
    LoggedIn,
    Spinner
  },
  data() {
    return {
      loginSelected: true,
      username: "",
      alreadyLoggedIn: null
    }
  },
  methods: {
    getDetails(){
      if (this.alreadyLoggedIn){
        setTimeout(() => {
          this.$store.dispatch('getDetails')
        }, 2000);
      } else {
        this.$store.dispatch('getDetails')
      }
    }
  },
  computed: {
    loggedIn() {
      return this.$store.getters.loggedIn;
    },
    userDetails() {
      return this.$store.getters.userDetails;
    }
  },
  mounted() {
    // If the user is logged in or gets event to login.
    if (this.loggedIn) {
      this.alreadyLoggedIn = true;
      this.getDetails();
    }
    bus.$on('getDetails', () => {
      this.getDetails();
    })
    bus.$on('turnOffAlreadyLoggedIn', () => {
      this.alreadyLoggedIn = null;
    })
  }
}
</script>


<style scoped>
.component-fade-enter-active, .component-fade-leave-active {
  transition: .3s ease;
}
.component-fade-enter, .component-fade-leave-to {
  transform: translateY(20px);
  opacity: 0;
}

.switch-selected-enter-active, .switch-selected-leave-active {
  transition: .3s ease;
}
.switch-selected-enter, .switch-selected-leave-to {
  transform: translateY(20px);
  opacity: 0;
}


.right-panel {
    width: 400px;
    height: 100%;
    background: rgba(0, 0, 0, 0.493);
    display: flex;
    flex-direction: column;
    overflow: auto;
    user-select: none;
}
.right-panel-inner{
    display: flex;
    flex-direction: column;
    height: 100%;
}
.new-member{
  display: flex;
  flex-direction: column;
  transition: .3s;
}

.logo {
  height: 150px;
  width: 150px;
  background: url(./../../assets/logo.png);
  background-size: 125%;
  background-position: center;
  border-radius: 50%;
  box-shadow: 0px 0px 96px -4px rgba(69,212,255,1);
  margin: auto;
  margin-bottom: 0;
  margin-top: 40px;
  flex-shrink: 0;

}

.title{
  color: white;
  font-size: 35px;
  text-align: center;
  margin-top: 120px;
  margin-bottom: 0 !important;
}

.right-panel .title {
    margin: auto;
    margin-top: 10px;
    width: 230px;
    font-size: 40px;
    text-align: center;
}
.details{
  color: rgb(204, 204, 204);
  margin: 40px;
  margin-top: 10px;
}
.switch-buttons{
    display: table;
    margin: auto;
}

.button{
    color: white;
    font-size: 18px;
    display: inline-block;
    padding: 10px;
    margin: 5px;
    user-select: none;
    transition: 0.3s;
}

.button:hover{
    border-bottom: solid 2px rgba(255, 255, 255, 0.493);
}

.button.selected{
    border-bottom: solid 2px white;
}
</style>
