import Vue from 'vue';
import Vuex from 'vuex';
import axios from './axios'
import {bus} from './main'
Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    token: localStorage.getItem('hauthid') || null,
    channelID: "",
    homePageRequestSent: {
      status: localStorage.getItem('hauthid') != null,
      message: "Getting your details..."
    },
    userDetails: null,
    messageLogs: {}
  },
  getters: {
    loggedIn(state) {
      return state.token !== null;
    },
    homePageRequestSent(state) {
      return state.homePageRequestSent;
    },
    userDetails(state){
      return state.userDetails;
    }
  },
  mutations: {
    sendMessage(state, message) {
      if (state.messageLogs[state.channelID] === undefined) {
        state.messageLogs[state.channelID] = {};
      }
      state.messageLogs[state.channelID][Date.now().toString()] = {channelID: state.channelID, message: message, messageID: Date.now(), status: 0};
    },
    homePageRequestSent(state, details) {
      state.homePageRequestSent = details;
    },
    login(state, token) {
      state.token = token
    },
    getDetails(state, details) {
      state.userDetails = details;
    },
    logout(state) {
      axios.defaults.headers.common['authorization'] = "";
      localStorage.removeItem('hauthid')
      state.userDetails = null,
      state.token = null;
    }
  },
  actions: {
    login(context, credentials) {
      return new Promise((resolve, reject) => {
        context.commit('homePageRequestSent', {status: true, message: "Logging you in..."});
        axios({
          url: "/user/login",
          method: "post",
          data: {email: credentials.email, password: credentials.password}
        })
        .then(response => {
          const token = response.data.token;
          localStorage.setItem('hauthid', token);
          axios.defaults.headers.common['authorization'] = token;
          context.commit('login', token);
          resolve(response)
        })
        .catch(error => {
          context.commit('homePageRequestSent', {status: false, message: ""});
          reject(error)
        })
      })
    },
    signup(context, account) {
      return new Promise((resolve, reject) => {
        context.commit('homePageRequestSent', {status: true, message: "Signing you up..."});
        axios({
          url: "/user/signup",
          method: "post",
          data: {email: account.email, username: account.username, password: account.password}
        })
        .then(response => {
          // Save token to local storage.
          const token = response.data.token;
          axios.defaults.headers.common['authorization'] = token;
          localStorage.setItem('hauthid', token);
          context.commit('login', token);
          resolve(response)
        }).catch(error => {
          context.commit('homePageRequestSent', {status: false, message: ""});
          reject(error)
        })
      })
    },
    getDetails(context) {
      return new Promise((resolve, reject) => {
        context.commit('homePageRequestSent', {status: true, message: "Getting your details..."});
        axios({
          url: "/user/details",
          method: "get"
        })
        .then (response => {
          const user = response.data.user;
          context.commit('getDetails', user);
          context.commit('homePageRequestSent', false, "");
          bus.$emit('turnOffAlreadyLoggedIn')
          resolve(response)
        })
        .catch(error => {
          if (error.response === undefined) {
            setTimeout(() => {
              context.dispatch('getDetails');
            }, 5000);
            return;
          }
          localStorage.removeItem('hauthid');
          context.commit('login', null);
          context.commit('homePageRequestSent', false, "");
          reject(error)
        })
      })
    },
    SOCKET_connect(context, payload) {
      console.log("connected")
      console.log(payload)
    }
  }
})