<template>
    <div class="message">
        <div class="profile-picture"></div>
        <div class="triangle">
            <div class="triangle-inner"></div>
        </div>
        <div class="content">
            <div class="username">Supertiger</div>
            <div class="content-message">{{this.$props.message}}</div>
        </div>
        <div class="sending-status">{{statusMessage}}</div>
    </div>
</template>


<script>
export default {
    props: ['message', 'status'],
    computed: {
        statusMessage(){
            let status = this.$props.status;

            if (status == 0) {
                return "Sending"
            } else if (status == 1) {
                return "Sent"
            } else if (status == 2) {
                return "Failed"
            } else {
                return ""
            }
        }
    }
}
</script>


<style scoped>

.message{
    margin: 10px;
    margin-left: 20px;
    margin-top: 20px;
    display: flex;
    align-content: center;
    align-items: center;
    animation: showMessage .3s ease-in-out;
}

@keyframes showMessage {
    from {
        transform: translate(0px, 9px);
        opacity: 0;
    }
}

.profile-picture{
    height: 50px;
    width: 50px;
    background-color: rgba(0, 0, 0, 0.281);
    border-radius: 50%;
    margin-right: 10px;
}

.triangle{
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.triangle-inner{
    width: 0;
    height: 0;

    border-top: 20px solid transparent;
    border-bottom: 20px solid transparent;
    border-right: 20px solid rgba(0, 0, 0, 0.301);
}

.content{
    background: rgba(0, 0, 0, 0.301);
    max-width: 50%;
    padding: 10px;

    display: flex;
    justify-content: center;
    flex-direction: column;
    border-radius: 10px;
    color: white;
}
.username {
    font-weight: bold;
}

.content-message {
    word-wrap: break-word;
}

.message .sending-status {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    padding-bottom: 5px;
    margin-left: 10px;
    font-size: 15px;
    color: white;
    align-self: normal;
}

</style>
