<template>
    <div class="my-mini-information">
        <div class="profile-picture"></div>
        <div class="information">
            <div class="name">Fishie</div>
            <div class="tag">#3864</div>

            <div class="status" v-on:click="status.isPoppedOut = !status.isPoppedOut"> 
                <img class="current-status" :src="status.url" />
                <i class="material-icons expand-status-icon">expand_more</i>
                <transition name="show-status-list">
                    <statusList v-if="status.isPoppedOut" v-click-outside="closeMenus" class="status-popout" />
                </transition>
            </div>

        </div>
    </div>
</template>

<script>
import {bus} from '../../main';
import statusList from '../../components/app/statusList.vue'

export default {
    components: {
        statusList
    },
    data() {
        return {
            status: {
                isPoppedOut: false,
                current: "0",
                url: require(`./../../assets/status/${"0"}.svg`)
            }
        }
    },
    methods: {
        //Changes the current status display
        changeStatus(status) {
            this.status.current = status,
            this.status.url = require(`./../../assets/status/${status}.svg`)
        },
        closeMenus() {
            this.status.isPoppedOut = false;
        }
    },
    created() {
        //When user changes their own status (statusList.vue)
        bus.$on('status-change', status => {
            this.changeStatus(status);
        })
    }
}
</script>



<style scoped>


.show-status-list-enter-active, .show-status-list-leave-active {
    transition: .1s;
}
.show-status-list-enter, .show-status-list-leave-to {
    opacity: 0;
    transform: translateY(10px);
}

.my-mini-information {
    border-bottom: solid 1px rgb(218, 218, 218);
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
}

.profile-picture{
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.377);
    margin-left: 10px;
    margin-right: 10px;
}

.information{
    color: white;
}

.status {
    display: inline-block;
    padding-top: 1px;
    padding-left: 5px;
    margin-left: 10px;
    transition: 0.3s;
    user-select: none;
    border-radius: 10px;

}

.status:hover {
    background: rgba(26, 25, 25, 0.349);
}

.expand-status-icon{
    opacity: 0;
    transition: 0.3s;
}

.status:hover .expand-status-icon{
    opacity: 1;
}

.status .current-status {
    width: 20px;
    height: 20px;
    background-size: 100%;
    background-position: center;
}

.name {
    margin-top: 10px;
}

.tag {
    color: rgb(199, 199, 199);
    font-size: 13px;
    display: inline-block;
    vertical-align: top;
    margin-top: 5px;
}



</style>
